.popup__title-update-avatar {
    max-width: 330px;
    margin: 0;
    padding: 34px 64px 54px 36px;

    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000;
}

@media screen and (max-width: 580px) {
    .popup__title-update-avatar {
        max-width: 238px;
        padding: 25px 22px 48px;
        font-size: 18px;
        line-height: 22px;
    }
}